

.carBoard{

    display: flex;
    flex-direction: column;
    width: 500px;
    height: fit-content;
    justify-content: center;
    align-items: center;
    border: 2px solid rgb(39, 14, 66);
    border-radius: 5px;
    padding: 5px;
    margin: 15px 0px;
    transition: all 0.8s;
}
.carBoard:hover{
    scale: 1.04;
    cursor: pointer;
}

.carBoard__inner{
    display:flex ;
    flex-wrap: wrap;
    width:300px;
}

.carBoard__inner__item{
    display: flex;
    align-items: center;
    gap: 6px;
    margin: 5px 10px;
}